import React, { useEffect, useState } from "react";
import CommentList from "./CommentList";
import { useDispatch, useSelector } from "react-redux";
import { getComment,addComment } from "../../redux/modules/commentSlice"
import Input from "../../elements/Input"

const CommentForm = () => {
  const [inputValue, setInputValue] = useState("")

  const dispatch = useDispatch();
  const commentList = useSelector((state) => state.commentSlice)

  useEffect(() => {
    dispatch(getComment());
  }, []);

  const onCreate = (e) => {
    e.preventDefault();
    if (inputValue) {
      const newList = { comment: inputValue };
      dispatch(addComment(newList));
      setInputValue("");
    } else {
      alert(".  ");
    }
  };

  return (
    <>
      <form action=''  onSubmit={onCreate}>
        <label></label>
        <Input
          type='text'
          onChange={(e) => setInputValue(e.target.value)}
          value={inputValue}
          // maxLength="35"
          placeholder=" ."
        />
        <button type='submit'>  </button>
        </form>
        <ul>
          {commentList.map((data)=> (
            <CommentList 
            key={data.id}
            commentId={data.id}
            username={data.username}
            comment={data.comment}
            />
          ))}
        </ul>
      

    </>
  );
};

export default CommentForm;
