import React from "react";
import styled from "styled-components";
import CollectionSlide from "../../elements/CollectionSlide";
import VideoList from "./VideoList";

const RandomCollections = () => {
  const temp = [
    { id: 0, title: "", img: "https://via.placeholder.com/150x150" },
    { id: 1, title: "", img: "https://via.placeholder.com/150x150" },
    { id: 2, title: "", img: "https://via.placeholder.com/150x150" },
    { id: 3, title: "", img: "https://via.placeholder.com/150x150" },
    { id: 4, title: "", img: "https://via.placeholder.com/150x150" },
  ];
  return (
    <section style={{ marginBottom: "50px" }}>
      <H1> ~</H1>
      <CollectionSlide>
        {temp?.map((data, idx) => {
          if (idx < 3) {
            return (
              <VideoList key={data.id} title={data.title} img={data.img} />
            );
          } else if (idx === 3) {
            return (
              <VideoList key={data.id} img={data.img}>
                
              </VideoList>
            );
          }
          return null;
        })}
      </CollectionSlide>
    </section>
  );
};
export default RandomCollections;
const H1 = styled.h1`
  font-size: 1.5rem;
  font-weight: bold;
  margin: 10px 0;
`;
