import React, { useEffect, useState } from "react";
import styled from "styled-components";
import Input from "../../elements/Input";
import Button from "../../elements/Button";
import useInputs from "../../hooks/useInput";
import icon_addvideo from "../../svg/icon_addvideo.svg";
import { useNavigate } from "react-router-dom";
import submit from "../../common/Confirm";
import { useDispatch, useSelector } from "react-redux";
import {
  getCategory,
  postCollection,
} from "../../redux/modules/collectionSlice";

const AddCollectionForm = () => {
  useEffect(() => {
    dispatch(getCategory());
  }, []);
  const nav = useNavigate();
  const dispatch = useDispatch();
  const categories = useSelector(
    (state) => state.myCollectionSlice.category.data
  );
  const videoList = useSelector((state) => state.myCollectionSlice.videoList);

  const [addVideo, setAddVideo] = useState(null);
  const [{ collectionTitle, description, category_id }, onChange, reset] =
    useInputs({
      collectionTitle: "",
      description: "",
      category_id: "0",
    });
  const videos = videoList.map((x) => x.videoId);
  const addData = { category_id, collectionTitle, description, videos };

  const onSubmitHandler = (e) => {
    console.log(videos);
    console.log(addData);
    e.preventDefault();
    if (
      collectionTitle === "" ||
      description === "" ||
      // addVideo === null ||
      category_id === "0"
    ) {
      alert(" ");
      return;
    } else {
      console.log("");
      dispatch(postCollection(addData));
    }
  };

  //todo required 
  //todo  disabled 

  return (
    <>
      <Title> </Title>
      <Button
        onClick={() => {
          nav(-1);
          //confirm test
          submit();
        }}
      >
        
      </Button>
      <Form onSubmit={onSubmitHandler}>
        <Button></Button>
        <Input
          placeholder="  "
          name="collectionTitle"
          onChange={onChange}
          value={collectionTitle}
        />
        <TextArea
          placeholder="  "
          name="description"
          onChange={onChange}
          value={description}
        />
        <Select name="category_id" onChange={onChange} value={category_id}>
          <Option value="0"> </Option>
          {categories?.map((option, idx) => {
            return (
              <Option value={option._id} key={idx}>
                {option.categoryName}
              </Option>
            );
          })}
        </Select>
        <ImgBox>
          <div>
            {" "}
              
            <Icon
              src={icon_addvideo}
              onClick={() => {
                nav("/mypage/add/search");
              }}
            ></Icon>
          </div>
          {videoList?.map((x, idx) => {
            return <div key={idx}>{x.title}</div>;
          })}
        </ImgBox>
      </Form>
    </>
  );
};
export default AddCollectionForm;
const Title = styled.h1``;
const Form = styled.form``;
const TextArea = styled.textarea``;
const Select = styled.select``;
const Option = styled.option``;
const ImgBox = styled.div`
  border-style: solid;
`;
const Icon = styled.img`
  width: 50px;
  height: 50px;
`;
